.banner {
  background: #234DA1;
  min-height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: center;

  & .center {
    display: flex;
    flex-direction: row;
    justify-content: center;
    position: relative;
  }
}

.banner_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 60px;
  line-height: 120%;
  text-align: center;
  color: #FFFFFF;
}

.banned_prev {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120%;
  color: #FFFFFF;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: absolute;
  left: 15px;
  top: calc(50% - (21px / 2));
  text-decoration: none;

  & svg {
    margin-right: 10px;
  }

  &:hover {
    text-decoration: underline;
  }
}

@media(max-width: 767px) {
  .banner {
    min-height: 250px;

    & .center {
      flex-direction: column;
      align-items: center;
    }
  }

  .banner_title {
    font-size: 40px;
    line-height: 120%;
  }

  .banned_prev {
    font-size: 16px;
    line-height: 140%;
    position: static;
    margin-bottom: 35px;
  }
}