.services {
  margin-top: 100px;
  margin-bottom: 140px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.services_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 120%;
  text-align: center;
  color: #100F0F;
  width: 100%;
  margin-bottom: 20px;
}

.services_item {
  padding: 30px;
  display: flex;
  flex-direction: column;
  min-height: 244px;
  box-sizing: border-box;
  border: 1px solid #CCCCCC;
  margin-top: 20px;
  flex-basis: 48.6%;
}

.services_img {
  margin-bottom: 20px;
}

.services_description {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 140%;
  color: #000000;
  margin-bottom: 30px;
}

.services_button {
  margin-top: auto;
  background: #3F8DEE;
  align-self: flex-start;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
  border: none;
  min-width: 150px;
  min-height: 50px;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    cursor: pointer;
    background: #2b4fa9;
  }
}

@media(max-width: 767px) {
  .services {
    margin-top: 80px;
    margin-bottom: 100px;
    flex-direction: column
  }

  .services_img {
    height: 34px;
  }

  .services_title {
    font-size: 26px;
  }

  .services_item {
    padding: 20px 15px;
    min-height: 213px;
  }
}